@import url(https://rsms.me/inter/inter.css);
* {
  box-sizing: border-box;
}

html,
body,
#root,
main {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  cursor: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMzIiIGhlaWdodD0iMzIiIHZpZXdCb3g9IjAgMCAzMiAzMiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48Y2lyY2xlIGN4PSIxNiIgY3k9IjE2IiByPSIxMCIgZmlsbD0iI0U4QjA1OSIvPjwvc3ZnPg=='),
    auto;
}

main {
  background: #f0f0f0;
  display: flex;
  flex-direction: row;
  position: relative;
}

.canvas {
  order: 2;
  flex: 1 1;
  height: 100%;
}

/* Overlay: centrado vertical a la izquierda */
.overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  pointer-events: none;
}

svg {
  width: 100%;
  height: 100%;
}

/* Responsive */
@media only screen and (max-aspect-ratio: 8/7) {
  main {
    flex-direction: column;
  }

  .canvas {
    order: 1;
  }

  svg {
    order: 2;
    width: 100%;
    max-width: 100%;
    height: 60vh;
  }
}

/* Bloque de texto */
.overlay-content {
  pointer-events: auto;
  max-width: 480px;
  margin-left: 40px;
  padding-left: 24px;
  border-left: 4px solid #e8b059;
}

.overlay-kicker {
  font-size: 11px;
  letter-spacing: 0.16em;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.overlay-title {
  font-size: 40px;
  line-height: 1.1;
  font-weight: 700;
  margin-bottom: 8px;
}

.overlay-subtitle {
  font-size: 16px;
  font-weight: 500;
  color: #e8b059;
  margin-bottom: 24px;
}

.overlay-body {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 24px;
}

.overlay-meta {
  font-size: 12px;
}

